body {
  margin: 50px auto;
  width: 306px;
}

tictoc-board > div {
  margin-bottom: 8px;
}

tictoc-board div.cells {
  display: grid;
  grid-template-columns: repeat(3, auto);
}

tictoc-board div.cells > button {
  background-color: #fff;
  width: 100px;
  height: 100px;
  box-shadow: 0 0 0 1px #333333;
  border: 1px solid #333333;
  cursor: pointer;
  line-height: 100px;
  font-size: 60px;
  text-align: center;
  z-index: 999;
}

tictoc-board div.cells > button:hover {
  background-color: #ededed;
}

tictoc-board div.cells > button[data-win-cell="true"] {
  color: green;
}
